export default class ArScreenshot {
    constructor (arViewer) {
        this.arViewer = arViewer;
    }

    screenshot() {
        const me = this;

        const videoEl = me.arViewer.arToolkitSource.domElement;
        const fieldWidth = me.arViewer.fieldEl.offsetWidth;
        const fieldHeight = me.arViewer.fieldEl.offsetHeight;

        const vCanvas = me.__createVideoCanvas(videoEl);
        const rCanvas = me.arViewer.renderer.domElement;

        const drawCanvas = document.createElement('CANVAS');
        const drawCtx = drawCanvas.getContext('2d');

        drawCanvas.width = fieldWidth;
        drawCanvas.height = fieldHeight;

        let gapX = (vCanvas.width - fieldWidth) / 2;
        let gapY = (vCanvas.height - fieldHeight) / 2;
        drawCtx.drawImage(vCanvas, gapX, gapY, fieldWidth, fieldHeight, 0, 0, fieldWidth, fieldHeight);

        gapX = (rCanvas.width - fieldWidth) / 2;
        gapY = (rCanvas.height - fieldHeight) / 2;
        drawCtx.drawImage(rCanvas, gapX, gapY, fieldWidth, fieldHeight, 0, 0, fieldWidth, fieldHeight);

        return drawCanvas;
    }

    __createVideoCanvas(videoEl) {
        //           .
        //         .
        //         .    ,      .
        const orgCanvasEl = document.createElement('CANVAS');
        const orgCtx = orgCanvasEl.getContext('2d');
        const videoW = videoEl.videoWidth;
        const videoH = videoEl.videoHeight;
        const videoOffW = videoEl.offsetWidth;
        const videoOffH = videoEl.offsetHeight;

        orgCanvasEl.width = videoW;
        orgCanvasEl.height = videoH;

        //     .
        orgCtx.drawImage(videoEl, 0, 0);

        const newCanvasEl = document.createElement('CANVAS');
        const newCtx = newCanvasEl.getContext('2d');
        const newSizeW = (videoOffH / videoH) * videoW;
        const newSizeH = videoOffH;
        const gapX = (videoOffW - newSizeW) / 2;
        const gapY = (videoOffH - newSizeH) / 2;

        newCanvasEl.width = videoOffW;
        newCanvasEl.height = videoOffH;

        //     .
        newCtx.drawImage(orgCanvasEl, gapX, gapY, newSizeW, newSizeH);

        return newCanvasEl;
    }
}