import Property from "./property";

export default class AssetManager {
    constructor(scene, assetLoader, assetCssRenderer) {
        this.__assetLoader = assetLoader;
        this.__assetCssRenderer = assetCssRenderer;
        this.__scene = scene;
        //        id  .
        this.__assetArrays = {};
        // ,        . (   )
        this.__assetArr = [];
        // ,       . (   )
        this.__assetItemArr = [];
    }

    /**
     *   .   ID     .
     * @param {Asset} asset ar   .
     */
    add(asset) {
        const me = this;

        asset.hide(false);

        me.__assetArrays[asset.controllerId] = [asset];

        me.__scene.add(asset.object3D);

        me.__assetCssRenderer.add(asset);

        me.__updateAssetArr();
    }

    /**
     *       .
     */
    getMarkerUrlArr() {
        const me = this;

        const arr = [];

        for (let key in me.__assetArrays) {
            if (me.__assetArrays.hasOwnProperty(key)) {
                arr.push(me.__assetArrays[key][0].markerImageUrl);
            }
        }

        return arr;
    }

    /**
     * 3D       .
     * @param {object} object3D   threejs 3D
     */
    getItemByObject3D(object3D) {
        const me = this;

        let item = null;

        for (let i = 0; i < me.__assetItemArr.length; i++) {
            if (me.__assetItemArr[i].object3D === object3D) {
                item = me.__assetItemArr[i];
                break;
            }
        }

        return item;
    }

    /**
     *        .     .
     * @param {string} name assetItem  .
     */
    getItemByName(name) {
        const me = this;

        let item = null;

        if (name) {
            for (let i = 0; i < me.__assetItemArr.length; i++) {
                if (me.__assetItemArr[i].name == name) {
                    item = me.__assetItemArr[i];
                    break;
                }
            }
        }

        return item;
    }

    /**
     *      .
     */
    getItemArray() {
        const me = this;

        return me.__assetItemArr;
    }

    /**
     *   asset   .
     * @param {number} clockDelta  
     */
    animation(clockDelta) {
        const me = this;

        let item;

        for (let i = 0; i < me.__assetItemArr.length; i++) {
            item = me.__assetItemArr[i];

            if (item.isAnimation && item.isVisible) {
                item.onAnimation(clockDelta);
            }
        }
    }

    /**
     *    .
     */
    recover() {
        const me = this;

        let asset;

        for (let i = 0; i < me.__assetArr.length; i++) {
            asset = me.__assetArr[i];

            if (asset.isUsed) {
                //      .
                //     borrow()     .
                asset.hideTimeout = me.__hideTimeout(asset);
                asset.isUsed = false;
            }
        }
    }

    /**
     *    .
     * @param {number} controllerId  ID.
     */
    borrow(controllerId) {
        const me = this;

        const assetArr = me.__assetArrays[controllerId];

        let asset = null;

        if (assetArr) {
            //  ID       .
            for (let i = 0; i < assetArr.length; i++) {
                const temp = assetArr[i];

                if (!temp.isUsed) {
                    temp.isUsed = true;

                    clearTimeout(temp.hideTimeout);

                    temp.show(true);

                    asset = temp;

                    break;
                }
            }

            //        .
            if (!asset) {
                me.caching(controllerId, 1);
            }
        }

        return asset;
    }

    /**
     *        .
     * @param {number} controllerId  ID.
     * @param {number} count .
     */
    caching(controllerId, count) {
        const me = this;

        const assetArr = me.__assetArrays[controllerId];
        const pArr = [];

        if (assetArr.length > 0) {
            for (let i = 0; i < count; i++) {
                pArr.push(me.__assetLoader.clone(assetArr[0]));
            }
        }

        return Promise.all(pArr).then(function (arr) {
            for (let j = 0; j < arr.length; j++) {
                const asset = arr[j];

                asset.hide(false);
                assetArr.push(asset);
                me.__scene.add(asset.object3D);
                me.__assetCssRenderer.add(asset);
            }

            me.__updateAssetArr();

            return Promise.resolve(arr);
        });
    }

    /**
     *    timeout id . (      )
     * @param {Asset} asset  
     */
    __hideTimeout(asset) {
        const me = this;

        return setTimeout(function () {
            asset.hideTimeout = null;

            const group = asset.object3D;

            let opacity = 1;

            asset.hideInterval = setInterval(function () {
                group.traverse(function(object3D) {
                    //   .
                    if (object3D.name != Property.TRANSPARENT_OBJECT_NAME && object3D.userData.hasOwnProperty(Property.ORG_TRANSPARENT_NAME)) {
                        object3D.material.transparent = true;
                        object3D.material.opacity = opacity;
                    }
                });

                opacity -= Property.SMOOTH_HIDE_INTERVAL;

                if (opacity <= 0) {
                    clearInterval(asset.hideInterval);

                    asset.hideInterval = null;
                    asset.trensformAverage = null;

                    asset.hide(true);

                    me.__assetCssRenderer.update(asset);

                    group.traverse(function(object3D) {
                        //  .
                        if (object3D.name != Property.TRANSPARENT_OBJECT_NAME && object3D.userData.hasOwnProperty(Property.ORG_TRANSPARENT_NAME)) {
                            object3D.material.transparent = object3D.userData[Property.ORG_TRANSPARENT_NAME];
                            object3D.material.opacity = object3D.userData[Property.ORG_OPACITY_NAME];
                        }
                    });
                }
            }, 10);
        }, Property.ASSET_TIMEOUT);
    }

    /**
     *  asset     asset    .
     */
    __updateAssetArr() {
        const me = this;
        const assetArr = [];

        let itemArr = [];

        let asset;

        for (let key in me.__assetArrays) {
            if (me.__assetArrays.hasOwnProperty(key)) {
                for (let i = 0; i < me.__assetArrays[key].length; i++) {
                    asset = me.__assetArrays[key][i];
                    assetArr.push(asset);
                    itemArr = itemArr.concat(asset.itemArr);
                }
            }
        }

        me.__assetArr = assetArr;
        me.__assetItemArr = itemArr;
    }
}