import Property from './property';

export default class Options {
    constructor (obj = {}) {
        /**
         *      .
         */
        this.baseUrl = obj.baseUrl || '.';

        /**
         * arToolkit   .
         */
        this.arToolkitBaseUrl = obj.arToolkitBaseUrl || '.';

        /**
         * arjs   .
         */
        this.sourceType = obj.sourceType || Property.SOURCE_TYPE_WEBCAM;

        /**
         * sourceType video  image   .
         */
        this.sourceUrl = obj.sourceUrl || '';

        /**
         * AR  .
         */
        this.rootElement = obj.rootElement || document.body;

        /**
         * AR .
         */
        this.width = parseFloat(obj.width) || Property.DEFAULT_WIDTH;

        /**
         * AR .
         */
        this.height = parseFloat(obj.height) || Property.DEFAULT_HEIGHT;

        /**
         *    .
         */
        this.useFrontCamera = !!obj.useFrontCamera;

        /**
         *    .
         */
        this.loadCachingCount = (typeof obj.loadCachingCount === 'number') ? obj.loadCachingCount : Property.LOAD_CACHING_COUNT;

        /**
         * AR   .
         */
        this.trensformAverageSize = (typeof obj.trensformAverageSize === 'number') ? obj.trensformAverageSize : Property.MATRIX_AVERAGE_SIZE;

        /**
         *      . 0.9 ~ 0.1       .
         */
        this.patternRatio = (typeof obj.patternRatio === 'number') ? obj.patternRatio : Property.PATTERN_RATIO;

        /**
         *    .
         */
        this.onInit = (typeof obj.onInit === 'function') ? obj.onInit : function () {};

        /**
         *     .
         */
        this.onError = (typeof obj.onError === 'function') ? obj.onError : function () {};

        /**
         * css      .
         */
        this.onPopup = (typeof obj.onPopup === 'function') ? obj.onPopup : function () {};

        /**
         * AR    .
         */
        this.onClick = (typeof obj.onClick === 'function') ? obj.onClick : function () {};

        /**
         * AR    .
         */
        this.onShowAsset = (typeof obj.onShowAsset === 'function') ? obj.onShowAsset : function () {};

        /**
         * AR    .
         */
        this.onHideAsset = (typeof obj.onHideAsset === 'function') ? obj.onHideAsset : function () {};
    }
}