export default class TrensformAverage {
    constructor (size) {
        this.__pArr = []; //   
        this.__sArr = [];
        this.__rArr = [];
        this.size = size;
        this.average = null;
    }

    add(object3D) {
        const me = this;

        if (me.__pArr.length >= me.size) {
            me.__pArr.shift();
            me.__sArr.shift();
            me.__rArr.shift();
        }

        me.__pArr.push(object3D.position.clone());
        me.__sArr.push(object3D.scale.clone());
        me.__rArr.push(object3D.rotation.clone());
    }

    getAverage() {
        const me = this;

        const pArr = me.__pArr;
        const sArr = me.__sArr;
        const rArr = me.__rArr;

        const length = pArr.length;
        const tempPosition = new THREE.Vector3();
        const tempScale = new THREE.Vector3();
        const tempRotation = new THREE.Euler();

        let i, position, scale, rotation;

        for (i = 0; i < length; i++) {
            position = pArr[i];
            scale = sArr[i];
            rotation = rArr[i];

            tempPosition.x += position.x;
            tempPosition.y += position.y;
            tempPosition.z += position.z;

            tempScale.x += scale.x;
            tempScale.y += scale.y;
            tempScale.z += scale.z;

            tempRotation.x += rotation.x;
            tempRotation.y += rotation.y;
            tempRotation.z += rotation.z;
        }

        tempPosition.x = tempPosition.x / length;
        tempPosition.y = tempPosition.y / length;
        tempPosition.z = tempPosition.z / length;

        tempScale.x = tempScale.x / length;
        tempScale.y = tempScale.y / length;
        tempScale.z = tempScale.z / length;

        tempRotation.x = tempRotation.x / length;
        tempRotation.y = tempRotation.y / length;
        tempRotation.z = tempRotation.z / length;

        return {
            position: tempPosition,
            scale: tempScale,
            rotation: tempRotation
        };
    }
}